* {
  margin: 0;
  padding: 0;
  box-sizing: content-box;
  font-family: "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Helvetica Neue", sans-serif;
}

body {
	width: 100%;
	height: 100%;
	background: #000;
	color: #fff;
}

.colorful-background {
	overflow-y: scroll;
	overflow-x: hidden;
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background: -webkit-linear-gradient(135deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
	background: linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
	background-size: 400% 400%;
}

.colorful-background.dynamic {
	-webkit-animation: Gradient 15s ease infinite;
	        animation: Gradient 15s ease infinite;
}

@-webkit-keyframes Gradient {
	0% { background-position: 0% 50% }
	50% { background-position: 100% 50% }
	100% { background-position: 0% 50% }
}

@keyframes Gradient {
	0% { background-position: 0% 50% }
	50% { background-position: 100% 50% }
	100% { background-position: 0% 50% }
}

.flex-wrapper {
  text-align: center;
}
/*
Social share button
*/

.button {
  display: block;
	margin: auto;
  border:none;
  background-color: #ececec;
  text-decoration: none;
  font-size: 18px;
  color: #FFF;
}

.socialfloat {
  display: block;
  position: fixed;
  left: 0;
  top: 20px;
  z-index: 9999;
}

.socialfloat .button {
  width: 40px;
  height: 40px;
  color: #fff;
}
.socialfloat svg {
	padding-top: 7px;
}

.button:hover {
    color: #efefef;
}

.facebook {
    background-color: #3b5998;
}
.gplus {
    background-color: #dd4b39;
}
.twitter {
    background-color: #55acee;
}
.pinterest {
    background-color: #cc2127;
}
.linkedin {
    background-color: #0077b5;
}

.flex-wrapper{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

main {
  flex: 1 1;
  min-width: 480px;
  width: 100%;
  padding-bottom: 30px;
}

@media only screen and (max-width: 600px) {
  main {
    min-width: 80%;
  }
}

.wrapper-width, main {
  max-width: 1024px;
  margin: 0 auto;
}

.songs-container {
  display: flex ;
  flex-wrap: wrap;
  justify-content: space-around;
}

.snowflake-card {
  text-align: center;
  margin-left: 10px;
  margin-right: 10px;
}

.title-wrap p {
  text-align: center;
  padding: 0;
}
.snowflake-card {
	color: #dadada;
	text-align: center;
	max-width: 300px;
}

.snowflake-card a {
	color: #fff;
	text-decoration: none;
	cursor: pointer;
	font-size: 0.8em;
}

.snowflake-card small {
	font-size: 0.8em;
	color: #dadada;
}

.snowflake {}

.snowflake-section path {
	stroke: #fff;
	stroke-width: 0.5px;
	fill-opacity: 0.3;
	fill: white;
	mix-blend-mode: luminosity;
}

.all-songs .snowflake:hover {
	-webkit-animation-name: spin;
	        animation-name: spin;
    -webkit-animation-duration: 2000ms;
            animation-duration: 2000ms;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}

@-webkit-keyframes spin {
	100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
	100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

header {
	padding: 1em;
	text-align: center;
}

header a {
	text-decoration: none;
	font-family: inherit;
	color: inherit;
}

header h1,
header h2 {
	font-family: 'Kaushan Script', 'Roboto',  "Ubuntu", "Cantarell", "Helvetica Neue", sans-serif;
	color: white;
	font-size: 3em;
}

header .autors
{
	font-size: 0.8em;
	padding: 1em;
}

header .autors a {
	text-decoration: underline;
	font-weight: bold;
}
footer {
	background: black;
	padding: 20px;
	border-top: 0.5px solid #555;
}

footer p {
	text-align: right;
	padding-bottom: 0.5em;
	font-size: 0.8em;
}

footer a {
	color: white;
	font-weight: bold;
}
.single-song {
	width: 100%;
}

.single-song h1 {
	font-size: 1.5em;
	padding-bottom: 0.5em;
}

.single-song .singer {
	font-size: 0.8em;
}

.single-song .content {
	display: flex;
	flex-direction: row;
	min-height: calc(100vh - 100px);
	align-items: center;
}

.single-song .navigation-button {
  display: flex;
  align-items: center;
  padding: 10px;
}

.single-song .grow {
	flex-grow: 1;
	display: flex;
	justify-content: center;
}

.single-song .background-player
{
	display: block;
	position: absolute;
	top: -9999px;
	left: -9999px;
	z-index: -10;
}
.about {
  text-align: justify;
  max-width: 480px;
  margin: 0 auto;
}

@media only screen and (max-width: 600px) {
  .about {
    padding: 20px;
    width: 80%;
  }
}

.about h2 {
	font-family: 'Kaushan Script', 'Roboto',  "Ubuntu", "Cantarell", "Helvetica Neue", sans-serif;
	font-size: 2em;
}

.about  p, h2, li, br {
  line-height: 1.5;
  margin-top: 15px;
}

.about p{
  font-size: 1.4em;
}

.about li {
  font-size: 1.1em;
}

.about ol {
  list-style-type: none;
  list-style-type: upper-roman;
  margin-left: 50px;
}

.svg-horiz-centered svg {
  display: block;
  margin: 30px auto;
}

.annotation-note-label, .about .color-legend p {
  font-size: 0.8em;
  color: lightgrey;
}

.annotation-note-title {
  fill: lightgrey;
}

.about a {
	text-decoration: underline;
	font-weight: bold;
	color: #4d4d4d;
}

.about a:hover {
	color: grey;
}

.about .colorful-rectangle {
	top: 0;
	left: 0;
	width: 300px;
	height: 25px;
	background: -webkit-linear-gradient(right, #EE7752, #E73C7E, #23A6D5, #23D5AB);
	background: linear-gradient(-90deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
	margin: 10px auto;
}

.about .color-legend {
  width: 300px;
  margin: 10px auto;
}

.about .color-legend-text {
  width: 150px;
  display: inline-block;
}

.about .color-legend p {
  text-align: left;
}

.about .right p {
  text-align: right;
}
.preloader {
  width: 40px;
  height: 40px;
  margin: 100px auto;
  background-color: #fff;

  border-radius: 100%;  
  -webkit-animation: circle-scaleout 2s infinite ease-in-out;
  animation: circle-scaleout 2s infinite ease-in-out;
}

@-webkit-keyframes circle-scaleout {
  0% { -webkit-transform: scale(0) }
  100% {
    -webkit-transform: scale(1.0);
    opacity: 0;
  }
}

@keyframes circle-scaleout {
  0% { 
    -webkit-transform: scale(0);
    transform: scale(0);
  } 100% {
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
    opacity: 0;
  }
}
